<template>
  <view class="h-full flex flex-col overflow-hidden">
    <view class="px-4 rounded-lg mx-4 mt-4 py-4 flex-none">
      <uv-steps v-bind="{ current: currentProgress.value, activeColor: activeColor }">
        <uv-steps-item title="信息填写"></uv-steps-item>
        <uv-steps-item title="手机号验证"></uv-steps-item>
        <uv-steps-item title="认证结果"></uv-steps-item>
      </uv-steps>
    </view>
    <view class="flex-1 h-0 overflow-auto">
      <view v-if="currentProgress.component === 'IdentityInfo'">
        <IdentityInfo v-bind="{ ...currentProgress, handler, params: formData }" />
      </view>

      <view v-if="currentProgress.component === 'ValidatePhone'">
        <ValidatePhone v-bind="{ ...currentProgress, handler, params: formData }" />
      </view>

      <view v-if="currentProgress.component === 'SuccessInfo'">
        <SuccessInfo v-bind="{ ...currentProgress, handler, params: formData }" />
      </view>
    </view>
  </view>
</template>

<script>
import IdentityInfo from './components/Identity/index.vue'
import SuccessInfo from './components/Success/index.vue'
import ValidatePhone from './components/ValidatePhone/index.vue'

export default {
  components: {
    IdentityInfo,
    ValidatePhone,
    SuccessInfo,
  },
  data() {
    return {
      progressActive: 0,
      progressModel: [
        {
          label: '信息填写',
          value: 0,
          component: 'IdentityInfo',
        },
        {
          label: '手机号验证',
          value: 1,
          component: 'ValidatePhone',
        },
        {
          label: '认证结果',
          value: 2,
          component: 'SuccessInfo',
        },
      ],
      formData: {},
    }
  },
  computed: {
    currentProgress() {
      return this.progressModel[this.progressActive] || {}
    },
    activeColor() {
      return this.$store.app.themeConfig['hubei-primary']
    },
  },
  methods: {
    handler({ active = 0, params = {} } = {}) {
      this.handleActive(active)
      this.formData = {
        ...this.formData,
        ...params,
      }
    },
    handleActive(value) {
      this.progressActive = value
    },
  },
}
</script>

<style lang="scss" scoped>
.h-full {
  background: white;
}

:deep {
  .uv-line-progress {
    overflow: hidden;
  }

  .uv-steps-item__wrapper,
  .uv-steps-item__wrapper--row {
    width: 20px;
    height: 20px;
    border-radius: 20px;
  }
}
</style>
